import { VhQueryCafe, VhAlgorithm, VhQuery } from 'ionic-vhframeworks';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss'],
})
export class AddComponent implements OnInit {
  public addCardForm: FormGroup;
  public isVisibleAddComponent = false;
  public tempVal: string = '';
  public listProduct: any[] = [];
  public listSearchProduct: any[] = [];
  public listProductChoosed: any[] = []; // Sản phẩm đã chọn
  public listProductMenu: any[] = [];
  total: number;
  list_barcode: any;
  barcode: boolean = false;
  heightScroll: string;
  editPrice: any = {};
  list_branch: any[];
  public price_origin: any;
  public localProducts: Array<any> = [];
  public indexTab = 0;
  public tabs: any = [
    this.languageService.translate('All'),
    'Product',
    'Service',
    'Ingredient',
    'Topping',
    'Combo',
  ];
  constructor(
    private router: Router,
    public languageService: LanguageService,
    public vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private vhQuery: VhQuery,
    private vhComponent: VhComponent,
    private cdRef: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    this.list_branch = this.vhQueryCafe.getlocalBranchs();
    this.localProducts = this.vhAlgorithm.sortDatebyASC(this.vhQueryCafe.getlocalDetailProducts().map((localProduct) => {
      if (localProduct.units) {
        localProduct.units = this.vhAlgorithm.sortNumberbyASC(
          localProduct.units,
          'ratio'
        );
        let unit = this.vhQueryCafe.getUnit_byRatio(localProduct.units, 1);
        return {
          ...localProduct,
          choose: false,
          display: 'flex',
          quantity: 1,
          ...unit,
        };
      } else
        return { ...localProduct, choose: false, display: 'flex', quantity: 1 };
    }), 'name');
    this.initForm();
    this.getBarcodes();
  }

  ngAfterViewChecked() {
    if (document.querySelector('.voucher-products'))
      this.heightScroll =
        document.querySelector('.voucher-products').clientHeight -
        document.querySelector('.ant-table-thead').clientHeight +
        'px';

    this.cdRef.detectChanges();
  }

  /**
   * get mẫu thiết kế thẻ trả trước
   */
  getBarcodes() {
    this.vhQuery
      .getDocsByFields("barcode_designs", {
        id_branch: { $eq: this.vhQueryCafe.getDefaultBranch()._id },
        type: { $eq: 4 },
      })
      .then((response: any) => {
        this.list_barcode = response.map((item) => {
          item.property = this.vhAlgorithm.sortNumberbyASC(
            item.property,
            'index'
          );
          return { ...item };
        });
      });
  }
  // khởi tạo form thêm payment card
  initForm() {
    this.addCardForm = new FormGroup({
      name: new FormControl('', [Validators.required]),
      unit: new FormControl('', [Validators.required]),
      points: new FormControl(0, [Validators.required]),
      id_design_barcode: new FormControl(''),
      price: new FormControl(0, [Validators.required]),
      price_origin: new FormControl(0, [Validators.required]),
      type: new FormControl(1),
    });
  }

  ngAfterViewInit() {
    this.vhAlgorithm.waitingStack().then(() => {
      this.price_origin = this.vhAlgorithm.vhnumeral('.price_origin');
    });
  }
  goBack() {
    this.router.navigate([
      'cafe/dashboard/management/sales-program/card-goods/list-card',
    ]);
  }

  /**
   * xác nhận tạo thẻ trả trước lên DB
   * @param data 
   */
  save(data) {
    data['units'] = [
      {
        price: this.total,
        price_origin: parseFloat(this.price_origin.getRawValue()),
        ratio: 1,
        unit: data.unit,
        points: data.points,
        default: true,
      },
    ];
    data['products'] = this.customizeListProduct(this.listProductChoosed);
    delete data.points;
    delete data.price;
    delete data.price_origin;
    delete data.unit;
    if (parseFloat(this.price_origin.getRawValue()) >= this.total) {
      this.vhComponent.showLoading('', 'transparent-loading').then(() => {
        this.vhQueryCafe
          .addPaymentCard({ ...data })
          .then((res: any) => {
            this.vhComponent.alertMessageDesktop(
              'success',
              res.name +
                ' ' +
                this.languageService.translate('has been added successfully')
            );
            this.goBack();
            this.vhComponent.hideLoading(0);
          })
          .catch((err) => {
            this.vhComponent
              .hideLoading(0)
              .then(() =>
                this.vhComponent.showToast(
                  1500,
                  this.languageService.translate(
                    'An error occurred while adding product. Please try again'
                  ),
                  'alert-toast'
                )
              );
          });
      });
    } else {
      this.vhComponent.alertMessageDesktop(
        'error',
        this.languageService.translate(
          'Giá niêm yết phải lớn hơn hoặc bằng giá bán'
        )
      );
    }
  }

  /**
   * xữ lý dữ liệu mảng sp đã chọn trong thẻ cho đúng chuẩn để lưu DB
   * @param listProductChoosed 
   * @returns array
   * let products = this.customizeListProduct([])
   */
  customizeListProduct(listProductChoosed: any[]): any {
    let newArr: any[] = [];
    listProductChoosed.forEach((element) => {
      let data = {
        price: element.price,
        id_product: element._id,
        ptype: element.type,
        quantity: element.quantity,
        ratio: element['unit_choosed'].ratio,
      };
      if (element.id_subproduct) data['id_subproduct'] = element.id_subproduct;
      newArr.push(data);
    });
    return newArr;
  }

  addProductCard() {
    this.changeTypeGood(this.indexTab);
    this.isVisibleAddComponent = true;
  }

  // xử lí chọn các sản phẩm trong payment card
  handleComponent() {
    for (let i = 0; i < this.listProduct.length; i++) {
      if (this.listProduct[i].choose == true) {
        if (
          this.listProductChoosed.filter(
            (item) => item.name == this.listProduct[i].name
          ).length == 0
        ) {
          // Xử lý đơn vị sản phẩm
          this.listProduct[i]['array_units'] = [];
          // Tạo đơn vị được chọn mặc định
          this.listProduct[i]['unit_choosed'] = this.listProduct[i].units[0];
          if (this.listProduct[i].units) {
            this.listProduct[i]['array_units'] = this.listProduct[i][
              'array_units'
            ].concat(this.listProduct[i].units);
          }
          this.listProductChoosed = this.listProductChoosed.concat([
            { ...this.listProduct[i] },
          ]);
          this.listProduct[i].display = 'none';
        }
      }
    }
    setTimeout(() => this.renderPrice(this.listProductChoosed), 200);
    this.cardPrice();
    this.isVisibleAddComponent = false;
  }

  handleCancelComponent() {
    this.isVisibleAddComponent = false;
  }

  /**
   * lọc sp theo tên
   * @param key 
   */
  searchProduct(key) {
    let tempVal: string = this.vhAlgorithm.changeAlias(key.toLowerCase());
    if (key.length) {
      this.listProduct = this.vhAlgorithm.searchList(
        tempVal,
        this.listSearchProduct,
        ['name', 'barcode']
      );
    } else {
      this.listProduct = this.listSearchProduct;
    }
  }
  // Tổng tiền trong voucher
  cardPrice() {
    this.total = 0;
    this.listProductChoosed.forEach((element) => {
      this.total = this.total + element.price * element.quantity;
    });
  }
  //  xóa sản phẩm trong voucher
  deleteItemChoosed(product) {
    this.listProductChoosed = this.listProductChoosed.filter(
      (item) => item.name != product.name
    );
    this.listProduct.map((item) => {
      if (item.name == product.name) {
        item.display = 'flex';
        item.price = product.price;
        item.choose = false;
      }
    });
    this.cardPrice();
  }

  /**
   * bắt sk thay đổi giá sp trong thẻ
   * @param item 
   */
  changePriceItemCard(item) {
    if (item) {
      let price: string = this.editPrice['product-' + item._id].getRawValue();
      item.price = price ? parseFloat(price) : item.price_origin;
    }
    this.cardPrice();
  }

  /**
   * tạo cleavejs cho price của sp trong thẻ để xử lý
   * @param arr 
   */
  renderPrice(arr: any[]) {
    arr.forEach((item) => {
      this.editPrice['product-' + item._id] = this.vhAlgorithm.vhnumeral(
        '.product-' + item._id
      );
    });
  }

  // cập nhật lại giá tiền khi đổi đơn vị
  changeUnit(ev, item) {
    let unit = this.vhQueryCafe.getUnit_byRatio(item.units, ev);
    if (unit) {
      item.unit = unit.unit
      item.price = unit.price
      item.price_import = unit.price_import
      item.price2 = unit.price2
      item.ratio = unit.ratio
    }
    this.cardPrice();
  }

  /**
   * bắt sk thay đổi giá niêm yết thẻ (giá gốc)
   */
  changePriceForm() {
    this.addCardForm.value.price_origin = parseFloat(
      this.price_origin.getRawValue()
    );
  }


  /**
   * bắt sk thay đổi tab xem loại sp
   * @param event 
   */
  changeTypeGood(event) {
    this.indexTab = event;
    if(this.indexTab == 0 ) this.listProduct = this.localProducts;
    else this.listProduct = this.localProducts.filter(filter => filter.type == this.indexTab)
    this.listSearchProduct = this.listProduct;
    this.searchProduct(this.tempVal);
  }
}
