import { VhQueryCafe, VhAlgorithm, VhQuery } from 'ionic-vhframeworks';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss'],
})
export class DetailComponent implements OnInit {
  public editCardForm: FormGroup;
  total: number;
  list_barcode: any;
  barcode: boolean = false;
  dataRestore: any =
    this.router.getCurrentNavigation().extras.state.dataRestore;
  data: any;
  show: any[] = [];
  heightScroll: string;
  list_branch: any[];
  price_origin: any;
  editPrice: any = {};
  constructor(
    private router: Router,
    public languageService: LanguageService,
    public vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private vhQuery: VhQuery,
    private vhComponent: VhComponent,
    private cdRef: ChangeDetectorRef
  ) {
    this.data = this.router.getCurrentNavigation().extras.state.data;
  }

  ngOnInit(): void {
    let localProducts: Array<any> = this.vhQueryCafe.getlocalDetailProducts();
    this.list_branch = this.vhQueryCafe.getlocalBranchs();
    this.initForm();
    this.getBarcodes();
    // xử lý phần sản phẩm trong thẻ để show ra giao diện bằng biến show
    if (this.data.products) {
      this.total = this.data.units[0].price;
      this.data.products.forEach((product, index) => {
        let localProduct = product.id_subproduct
          ? localProducts.find(
              (find) => find.id_subproduct == product.id_subproduct
            )
          : localProducts.find((find) => find._id == product.id_product);
        this.show.push({
          ...product,
          price_origin: product.price,
          index: index,
          name: localProduct.name,
          img: localProduct.img,
          unit: this.vhQueryCafe.getUnit_byRatio(
            localProduct.units,
            product.ratio
          ).unit,
        });
      });
    }
    this.vhAlgorithm.waitingStack().then(() => {
      this.renderPrice(this.data.products);
    });
  }

  ngAfterViewChecked() {
    if (document.querySelector('.voucher-products'))
      this.heightScroll =
        document.querySelector('.voucher-products').clientHeight -
        document.querySelector('.ant-table-thead').clientHeight +
        'px';

    this.cdRef.detectChanges();
  }

  ngAfterViewInit() {
    this.vhAlgorithm.waitingStack().then(() => {
      this.price_origin = this.vhAlgorithm.vhnumeral('.price_origin');
    });
  }

  // khởi tạo form edit
  initForm() {
    this.editCardForm = new FormGroup({
      name: new FormControl(this.data.name, [Validators.required]),
      id_design_barcode: new FormControl(this.data.id_design_barcode),
      points: new FormControl(this.data.units[0].points, [Validators.required]),
      unit: new FormControl(
        this.data.units[0].unit,
        Validators.compose([Validators.required])
      ),
      price: new FormControl(
        this.data.units[0].price,
        Validators.compose([Validators.required])
      ),
      price_origin: new FormControl(
        this.data.units[0].price_origin,
        Validators.compose([Validators.required])
      ),
      type: new FormControl(this.data.type),
      products: new FormControl(this.data.products),
    });
  }

  // lấy danh sách barcode của payment card type = 4
  getBarcodes() {
    this.vhQuery
      .getDocsByFields("barcode_designs", {
        id_branch: { $eq: this.vhQueryCafe.getDefaultBranch()._id },
        type: { $eq: 4 },
      })
      .then((response: any) => {
        this.list_barcode = response.map((item) => {
          item.property = this.vhAlgorithm.sortNumberbyASC(
            item.property,
            'index'
          );
          return { ...item };
        });
      });
  }

  goBack() {
    this.router.navigate([
      'cafe/dashboard/management/sales-program/card-goods/list-card',
    ]);
  }

  // xóa payment card
  deleteCard() {
    this.vhComponent.showLoading('', 'transparent-loading').then(() => {
      this.vhQueryCafe.deletePaymentCard(this.data._id).then(
        (rsp: any) => {
          this.vhComponent.hideLoading(0);
          if (rsp.vcode == 0) {
            this.vhComponent.alertMessageDesktop(
              'success',
              this.languageService.translate('Xóa Thẻ trả trước thành công')
            );
            this.goBack();
          } else if (rsp.vcode == 1) {
            this.vhComponent.alertMessageDesktop(
              'error',
              this.languageService.translate(
                'Xóa không thành công, vui lòng xóa Thẻ này khỏi đợt phát hành liên quan!'
              ),
              8000
            );
          }
        },
        (error) => {
          this.vhComponent.hideLoading(0);
          console.log('error', error);
        }
      );
    });
  }

  /**
   * bắt sk thay đổi giá niêm yết thẻ (giá gốc)
   */
  changePriceForm() {
    this.editCardForm.value.price_origin = parseFloat(
      this.price_origin.getRawValue()
    );
  }

  // Tổng tiền trong thẻ
  cardPrice() {
    this.total = 0;
    this.show.forEach((element) => {
      this.total = this.total + element.price * element.quantity;
    });
    this.updatePaymentCard('products', {products: this.show});
    this.updatePaymentCard('price', {price: this.total})
  }

  /**
   * bắt sk thay đổi giá sp trong thẻ
   * @param item 
   */
  changePriceItemCard(item, index) {
    if (item) {
      let price: string = this.editPrice['product-' + index].getRawValue();
      item.price = price ? parseFloat(price) : item.price_origin;
    }
    this.cardPrice();
  }

  /**
   * tạo cleavejs cho price của sp trong thẻ để xử lý
   * @param arr 
   */
  renderPrice(arr: any[]) {
    arr.forEach((item, index) => {
      this.editPrice['product-' + index] = this.vhAlgorithm.vhnumeral(
        '.product-' + index
      );
    });
  }

  updatePaymentCard(field, object) {
    if (!this.editCardForm.valid) return;
    if(field == 'price_origin' || field == 'price'){
      if (parseFloat(this.editCardForm.value.price_origin) < this.total)
      return this.vhComponent.alertMessageDesktop(
        'error',
        this.languageService.translate(
          'Giá niêm yết phải lớn hơn hoặc bằng giá bán'
        )
      );
    }

    if (field == 'points' || field == 'price_origin' || field == 'price') {
      object = {
        units: [
          {
            ratio: 1,
            default: true,
            points: this.editCardForm.value.points,
            price_origin: this.editCardForm.value.price_origin,
            price: this.total,
            unit: this.editCardForm.value.unit,
          },
        ],
      };
    }
    this.vhQueryCafe.updatePaymentCard(this.data._id, object).then(
      (bool) => {
        console.log('updateDoc', bool);
      },
      (error) => {
        console.log('error', error);
      }
    );
  }
}
